import { Component } from 'angular2/core';

/**
 * Represents the modal backdrop.
 */
@Component({
    selector: 'modal-backdrop',
    host: {
        'class': 'in modal-backdrop'
    },
    template: ''
})
export class ModalBackdrop {
    constructor() {}
}


